import * as f from "../functions.js"
import { player } from "../player.js"
import effects from "../buffsManager.js"

export class basicAttackCardClass{
static generatedCardType = "attack"

  constructor( name , energyCost , damage){
    this.name = name
    this.energyCost = energyCost
    this.damage = damage
    


    this.target = "enemy"
    this.type = "attack"
  }
  defaultPlay(enemy){
    // console.log(this.name , "attacked" , enemy.name , "for" , this.damage);
    f.damage(enemy, player , this.damage)
    this.discard()
  }
  discard(){
    f.discardCard(this)

  }
    attack(enemy){
      f.damage(enemy  , player , this.damage)
    }
}




export class strike extends basicAttackCardClass {
  constructor(damage,energyCost){
    super("Strike" , 1 ,6 )
  }
  play(enemy){
    this.defaultPlay(enemy)
  }
}


export class bash extends basicAttackCardClass{
  constructor(damage,energyCost){
    super("Bash" , 2 ,10) 
  }

  play(enemy){
    this.defaultPlay(enemy)
    enemy.apply(new effects.buffer())
    enemy.apply(new effects.vulnerable(3))
  }
}


