import {applyEffectHandler  , applyStackingEffectHandler, reduceEffectDuration , reduceEffectUses} from "./effectsHandler.js"
import {addTodealDamageEffects ,addTorecieveDamageEffects ,addToblockEffects ,addToturnEffects ,addTocardEffects} from "./effectsHandler.js"
import { removeFromdealDamageEffects , removeFromrecieveDamageEffects ,removeFromblockEffects ,removeFromturnEffects ,removeFromcardEffects } from "./effectsHandler.js"
import pubsub from "../pubsub.js"

export class basicBuff{
static generatedEffect = "buffs"

  constructor(name,duration , uses){
    this.name = name

    this.duration = duration ?? "inf"
    this.uses = uses ?? "ind"

    this.hostingEntityHasIt = false
  }
 
  apply(entity){
    this.init(entity)
  }
  remove(){
    this.defaultRemove()
  }

  defaultInit(entity){
// for when first applied
    applyEffectHandler(this,entity)
  }
  defaultStackingInit(entity){
    applyStackingEffectHandler(this . entity)
  }


  effect(){
// subs to pubsub event and triggers
  }
  defaultRemove(){
// for removal
    delete this.hostingEntity.debuffs[this.name]
    delete this
  }

}

export class buffer extends basicBuff{
  constructor(){
    super("buffer" , null , 1 )
  }

  init(entity){
     this.defaultInit(entity)
  if(this.alreadyAplied){return}
     addTorecieveDamageEffects(this)
     
  }


  listEffect(damage){
    if (damage.val >= 0) {
      damage.val = 0
    }
    reduceEffectUses(this)
  }

  remove(){
    this.defaultRemove()
    removeFromrecieveDamageEffects(this)
  }
}

