import {applyEffectHandler ,applyStackingEffectHandler, reduceEffectDuration , reduceEffectUses , halfEffectDuration} from "./effectsHandler.js"
import {addTodealDamageEffects ,addTorecieveDamageEffects ,addToblockEffects ,addToturnEffects ,addTocardEffects} from "./effectsHandler.js"
import { removeFromdealDamageEffects , removeFromrecieveDamageEffects ,removeFromblockEffects ,removeFromturnEffects ,removeFromcardEffects } from "./effectsHandler.js"
import {directlyDamage} from "../functions.js"
import pubsub from "../pubsub.js"

export class basicDebuff{
static generatedEffect = "debuffs"

  constructor(name , duration , uses){
    this.name = name

    this.duration = duration ?? "inf"
    this.uses = uses ?? "ind"

    this.alreadyAplied = false
  }

  apply(entity){
    this.init(entity)
  }
  remove(){
    this.defaultRemove()
  }

  defaultInit(entity){
// for when first applied
    applyEffectHandler(this,entity)
  }
  effect(){
// subs to pubsub event and triggers
  }
  defaultRemove(){
// for removal
    delete this.hostingEntity.debuffs[this.name]
    delete this
  }
}


export class vulnerable extends basicDebuff{
  constructor(duration){
    super("Vulnurable" , duration , "inf" )
  }

  init(entity){
    this.defaultInit(entity)
  if(this.alreadyAplied){return}

    pubsub.on("turnStarted" , this)
    entity.defMod += 0.5
  }


  effect(){
    console.log( "enemy every turn defmod:" , this.hostingEntity.defMod)
//     console.log( `enemy every turn :${variable}:` , this.hostingEntity)
// variable++
    reduceEffectDuration(this)
    
  }

  remove(){
    this.hostingEntity.defMod -= 0.5
    console.log( "enemy def mod at end :",this.hostingEntity.defMod);
    pubsub.off("turnStarted" , this)
    this.defaultRemove()
  }
}

export class poison extends basicDebuff{
  constructor(duration ){
    super("Poison" , duration , "inf" )
  }

  init(entity){
     this.defaultInit(entity)
  if(this.alreadyAplied){return}
    pubsub.on("turnStarted" , this)
  }

  effect(turn){
    directlyDamage(this.duration , this.hostingEntity)
    reduceEffectDuration(this)
  }

  remove(){
  pubsub.off("turnStarted" , this)
    this.defaultRemove()
  }
}

export class bleed extends basicDebuff{
  constructor(duration ){
    super("Bleed" , duration , "inf" )
  }

  init(entity){
     this.defaultInit(entity)
  if(this.alreadyAplied){return}
    pubsub.on("turnStarted" , this)
  }

  effect(turn){
    directlyDamage(this.duration , this.hostingEntity)
    halfEffectDuration(this)
  }

  remove(){
  pubsub.off("turnStarted" , this)
    this.defaultRemove()
  }
}

