import {playerStartingDeck} from "./cards/01cardsManager.js"
import {setUpHealthBar} from "./enemies/enemyClass.js"

class playerClass{
  constructor(hp,deck,relics){
    this.maxHealth = hp
    this.health = hp
    this.block = 30
    this.energy = 3

    this.hand = []
    this.drawPile = []
    this.discardPile = []

    this.buffs = {}
    this.debuffs = {}

        this.dealDamageEffects = {}
        this.recieveDamageEffects = {}
        this.blockEffects = {}
        this.cardEffects = {
          playEffects : {} ,
          drawEffects: {
            prop: "Hi Im DrawEffects prop and I apply to " ,
            prop2 : this
          },
          exaustEffects : {},
          discardEffects : {}
        }
        this.turnEffects = {}

    this.str = 0
    this.dex = 0
    this.atkMod = 1
    this.defMod = 1

    this.actualDeck = deck
    this.deck = deck
    this.relics = relics
// dom stufff
this.modal = document.querySelector(".player")
this.modal.entity = this
      setUpHealthBar(this)
  }

  apply(effectObj){
    effectObj.apply(this)
  } 
  remove(effectName){
    this.buffs[effectName]?.remove()
    this.debuffs[effectName]?.remove()
  }
  
  test(){
    console.log(this.cardEffects.drawEffects.prop ,this.cardEffects.drawEffects.prop2 );
  }
}

export const player = new playerClass(73,playerStartingDeck,{})
